////User data with backend///////
class UserModel {
  #baseURL = "http://localhost:3000/api";

  async getAllUsers() {
    try {
      const res = await fetch(`${this.#baseURL}/users`);
      if (!res.ok) throw new Error(`HTTP error! status:${res.status}`);

      const data = await res.json();
      if (!data || !data.users) return [];

      return data.users;
    } catch (err) {
      console.error(`Error fetching users: ${err}`);
      return [];
    }
  }

  async getUser(id) {
    try {
      const res = await fetch(`${this.#baseURL}/users/${id}`);

      const data = await res.json();

      if (!res.ok)
        return {
          success: false,
          error: data.error || "Request failed",
          errors: data.errors || [],
          statusCode: res.status,
        };

      if (!data || !data.user) return null;

      return data.user;
    } catch (err) {
      console.error(`Error fetching user: ${err.message}`);
      return null;
    }
  }

  async createUser(userData) {
    try {
      const res = await fetch(`${this.#baseURL}/users`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userData),
      });

      const data = await res.json();

      if (!res.ok)
        return {
          success: false,
          error: data.error || "Request failed",
          errors: data.errors || [],
          statusCode: res.status,
        };

      if (!data) return null;

      return data;
    } catch (err) {
      console.error(`Error creating user: ${err}`);
      return { success: false, error: err.message };
    }
  }

  ///PUT fill-replacement
  async replaceUser(id, userData) {
    try {
      const res = await fetch(`${this.#baseURL}/users/${id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userData),
      });

      const data = await res.json();

      if (!res.ok)
        return {
          success: false,
          error: data.error || "Request failed",
          errors: data.errors || [],
          statusCode: res.status,
        };

      return data;
    } catch (err) {
      console.error(`Error putting user: ${err}`);
      return {
        success: false,
        error: err.message,
      };
    }
  }

  ///PATCH partial-replacement
  async partiallyReplaceUser(id, userData) {
    try {
      const res = await fetch(`${this.#baseURL}/users/${id}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userData),
      });

      const data = await res.json();

      if (!res.ok)
        return {
          success: false,
          error: data.error || "Request failed",
          errors: data.errors || [],
          statusCode: res.status,
        };

      return data;
    } catch (err) {
      console.error(`Error patching user: ${err}`);
      return {
        success: false,
        error: err.message,
      };
    }
  }

  async deleteUser(id) {
    try {
      const res = await fetch(`${this.#baseURL}/users/${id}`, {
        method: "DELETE",
      });

      const data = await res.json();

      if (!res.ok)
        return {
          success: false,
          error: data.error || "Request failed",
          errors: data.errors || [],
          statusCode: res.status,
        };
      return data;
    } catch (err) {
      console.error(`Error deleting User: ${err}`);
      return {
        success: false,
        error: err.message,
      };
    }
  }
}

